// Step name config
export const getStepName = (stepId, lecturerName) => {
    switch (stepId) {
        // Register
        case 100:
            return `Xin ng k  ti vi ging vin ${lecturerName}`
        case 4000:
            return `Ging vin ${lecturerName} t chi hng dn  ti`
        case 4001:
            return `Ging vin ${lecturerName} chp nhn hng dn  ti`
        case 4002:
            return `Ging vin ${lecturerName} chnh sa  ti`
        case 200:
            return `Xut trnh  ti`
        case 210:
            return `Xc thc ging vin n v ngoi cho  ti`
        case 212:
            return `Xc thc ging vin n v ngoi cho yu cu thay i  ti`
        case 300:
            return `Ghi nhn ph duyt  ti`
        case 5000:
            return `B mn thm nh v t chi`
        case 5001:
            return `B mn thm nh v chp nhn`
        case 5002:
            return `B mn thm nh v yu cu chnh sa`
        case 5003:
            return `B mn phn ging vin ${lecturerName} thm nh  ti`
        case 2100:
            return `Khoa cho php chnh sa li yu cu  ti`
        // Editing
        case 101:
            return `Xin iu chnh  ti`
        case 4010:
            return `Ging vin ${lecturerName} t chi  ngh chnh sa`
        case 4011:
            return `Ging vin ${lecturerName} chp nhn  ngh chnh sa`
        case 2010:
            return `Khoa t chi  ngh chnh sa`
        case 2011:
            return `Xut trnh  ngh chnh sa`
        case 211:
            return `Khoa m t iu chnh`
        case 2012:
            return `Khoa ng t iu chnh`
        case 301:
            return `Ghi nhn ph duyt  ngh chnh sa`
        // Extending
        case 102:
            return `Xin gia hn  ti`
        case 4020:
            return `Ging vin ${lecturerName} t chi  ngh xin gia hn`
        case 4021:
            return `Ging vin ${lecturerName} chp nhn  ngh xin gia hn`
        case 2020:
            return `Khoa t chi  ngh gia hn`
        case 2021:
            return `Xut trnh  ngh gia hn`
        case 2022:
            return `Khoa chnh sa  ngh gia hn`
        case 302:
            return `Ghi nhn ph duyt  ngh gia hn`
        // Cancel
        case 103:
            return `Xin thi lm  ti`
        case 4030:
            return `Ging vin ${lecturerName} t chi  ngh xin thi`
        case 4031:
            return `Ging vin ${lecturerName} chp nhn  ngh xin thi`
        case 2030:
            return `Khoa t chi  ngh xin thi`
        case 2031:
            return `Xut trnh  ngh xin thi`
        case 303:
            return `Ghi nhn ph duyt  ngh xin thi`
        // Pause
        case 104:
            return `Xin tm hon lm  ti`
        case 4040:
            return `Ging vin ${lecturerName} t chi  ngh tm hon`
        case 4041:
            return `Ging vin ${lecturerName} chp nhn  ngh tm hon`
        case 2040:
            return `Khoa t chi  ngh tm hon`
        case 2041:
            return `Xut trnh  ngh tm hon`
        case 2042:
            return `Khoa chnh sa  ngh tm hon`
        case 304:
            return `Ghi nhn ph duyt  ngh tm hon`
        default:
            return `Thao tc cha c trong danh sch`
        // Defense register
        case 105:
            return `Hc vin ng k bo v`
        case 4050:
            return `Ging vin ${lecturerName} t chi ng k bo v ca hc vin`
        case 4051:
            return `Ging vin ${lecturerName} chp nhn ng k bo v ca hc vin`
        case 2051:
            return `Khoa nh du  ti  np quyn`
        case 2049:
            return `Khoa nh du  ti cha np quyn`
        case 2050:
            return `Khoa t chi  ngh ng k bo v`
        case 2052:
            return `Xut trnh  ngh ng k bo v`
        case 305:
            return `Ghi nhn ph duyt  ngh ng k bo v`
        case 2053:
            return `Khoa m t ng k bo v`
        case 2054:
            return `Khoa ng t ng k bo v`
        case 2081:
            return `Khoa nh du  ti bo v thnh cng`
        case 2080:
            return `Khoa nh du  ti bo v khng thnh cng`
        case 2082:
            return `Khoa nh du  ti qu hn`
        // Seminar
        case 106:
            return `Hc vin ng k seminar`
        case 2090:
            return `Khoa m quyn ng k seminar`
        case 2091:
            return `Khoa nh du  ti  seminar`
        case 4060:
            return `Ging vin ${lecturerName} t chi ng k seminar ca hc vin`
        case 4061:
            return `Ging vin ${lecturerName} chp nhn ng k seminar ca hc vin`
    }
}



